<template>
    <div class='pageWrap'>
        <Top :right-action='rightAction' @rightClick='changeLoginType' />
        <header class='header'>
            <h1 class='pageTitle'>
                {{ $t(loginType==='password'?'login.loginByPwd':'login.loginByCode') }}
            </h1>
        <!-- <LanguageDiv /> -->
        </header>
        <form class='loginForm'>
            <div v-if="loginAccount==='mobile'" class='field'>
                <!-- <areaInput v-model.trim='loginName' v-model:zone='zone' clear placeholder='请输入手机号或邮箱' /> -->
                <InputComp v-model.trim='loginName' clear :label="$t('login.loginNamePlaceholder')" />
            </div>
            <div v-else class='field'>
                <InputComp v-model.trim='email' clear :label="$t('login.email')" />
            </div>
            <div v-if="loginType==='password'" class='field'>
                <InputComp v-model='pwd' clear :label="$t('login.pwd')" pwd />
            </div>
            <div v-else class='field'>
                <CheckCode v-model.trim='checkCode' clear :label="$t('login.verifyCode')" @verifyCodeSend='verifyCodeSendHandler' />
            </div>
            <van-button block class='loginBtn' :disabled='loading' type='primary' @click='loginHandle'>
                {{ $t('login.loginBtn') }}
            </van-button>
            <div class='toolBtns'>
                <a class='btn' href='javascript:;' @click="$router.push({ name:'Register' })">
                    {{ $t('login.register') }}
                </a>
                <Vline />
                <a class='btn' href='javascript:;' @click="$router.push({ name:'Forgot' })">
                    {{ $t('login.forgot') }}
                </a>
            </div>
        </form>
    <!-- <div class='otherLogin'>
            <LoginByGoogle />
            <span class='empty'></span>
            <LoginByFacebook />
        </div> -->
    <!-- <footer class='footer'>
            <a class='link' href='javascript:;'>
                <i class='icon_icon_service'></i>
                在线客服
            </a>
        </footer> -->
    </div>

    <!-- 设置登录密码 -->
    <van-popup v-model:show='loginPwdPop' :close-on-click-overlay='false' :style="{ 'border-radius':'8px','background-color': $style.bgColor }">
        <section class='popContainer'>
            <a class='noTip' href='javascript:;' @click='noTip'>
                {{ $t('login.neverTip') }}
            </a>
            <div class='containerBox'>
                <p class='iconPwd'>
                    <span class='icon_password'></span>
                </p>
                <p class='tipContent'>
                    {{ $t('login.pwdTips') }}
                </p>
            </div>
            <div class='btnBox'>
                <button class='btn' @click='loginPwdSet'>
                    {{ $t('login.goSet') }}
                </button>
                <button class='btn muted' @click='loginPwdSetNext'>
                    {{ $t('login.nextTime') }}
                </button>
            </div>
        </section>
    </van-popup>
    <Loading :show='loading' />
</template>

<script>
import Schema from 'async-validator'
import LanguageDiv from '@m/modules/languageDiv'
import areaInput from '@/components/form/areaInput'
import InputComp from '@/components/form/input'
import Vline from '@/components/vline'
import CheckCode from '@/components/form/checkCode'
import LoginByGoogle from '@/components/loginByGoogle/loginByGoogle'
import LoginByFacebook from '@/components/loginByFacebook/loginByFacebook'
import Top from '@/components/top'
import { getDevice, localGet, localSet, getArrayObj } from '@/utils/util'
import { verifyCodeSend } from '@/api/base'
import { computed, reactive, toRefs, getCurrentInstance } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useStore } from 'vuex'
import { Toast, Dialog } from 'vant'
import RuleFn from './rule'
import md5 from 'js-md5'
import { timeline, timelineItem } from '@/components/timeline'
import { checkUserStatus } from '@/api/user'
import { useI18n } from 'vue-i18n'

export default {
    components: {
        timeline,
        timelineItem,
        LanguageDiv,
        Vline,
        InputComp,
        areaInput,
        LoginByGoogle,
        LoginByFacebook,
        CheckCode,
        Top,
    },
    setup () {
        const router = useRouter()
        const route = useRoute()
        const store = useStore()
        const { t } = useI18n({ useScope: 'global' })
        const instance = getCurrentInstance()
        const state = reactive({
            loading: false,
            pwdVisible: false,
            loginPwdPop: false,
            loginName: '',
            pwd: '',
            checkCode: '',
            loginType: 'checkCode',
            loginAccount: 'mobile',
        })
        let token = ''
        const rightAction = computed(() => {
            return {
                title: t(state.loginType === 'password' ? 'login.loginByCode' : 'login.loginByPwd')
            }
        })

        const changeLoginType = () => {
            const loginType = state.loginType
            state.loginType = loginType === 'password' ? 'checkCode' : 'password'
        }
        const loginHandle = () => {
            const loginParams = {
                type: state.loginName.includes('@') ? 1 : 2,
                loginName: state.loginName,
                device: getDevice(),
                verifyCode: state.loginType === 'checkCode' ? state.checkCode : undefined,
                loginPwd: state.loginType === 'password' ? md5(state.pwd) : undefined,
                sendToken: state.loginType === 'checkCode' ? token : undefined,

            }

            const validator = new Schema(RuleFn(t))
            state.loading = true

            validator.validate(
                {
                    ...loginParams
                }, {
                    ...state,
                    first: true
                }, (errors, fields) => {
                    // console.log(errors, fields)
                    if (errors) {
                        state.loading = false
                        Toast(errors[0].message)
                        return
                    }
                    loginSubmit(loginParams)
                })
        }

        // 登录成功跳转
        const loginToPath = () => {
            const toURL = route.query.back ? decodeURIComponent(route.query.back) : '/'
            router.replace(toURL)
        }

        // 发送登录接
        const loginSubmit = (params) => {
            store.dispatch('_user/login', params).then(res => {
                state.loading = false
                // console.log(res)
                if (res.invalid()) return false

                // 登录websocket
                instance.appContext.config.globalProperties.$MsgSocket.login()
                // 重新登录清除账户信息
                store.commit('_user/Update_accountAssets', {})

                // 登录KYC,kycAuditStatus:0未认证跳,需转到认证页面,1待审核,2审核通过,3审核不通过
                // companyKycStatus 公司KYC开户状态，1开启 2未开启
                if (Number(res.data.companyKycStatus) === 1) {
                    if (Number(res.data.kycAuditStatus === 0)) {
                        return Dialog.alert({
                            title: t('common.tip'),
                            confirmButtonText: t('login.goAuthenticate'),
                            message: t('login.goAuthenticateMsg'),

                        }).then(() => {
                            router.push('/authentication')
                        })
                    } else if (Number(res.data.kycAuditStatus === 1)) {
                        return Dialog.alert({
                            title: t('common.tip'),
                            confirmButtonText: t('common.close'),
                            message: t('common.inReview'),

                        }).then(() => {
                            store.dispatch('_user/logout').then(() => {
                                return router.push('/login')
                            }).then(() => {
                                location.reload()
                            })
                        })
                    } else if (Number(res.data.kycAuditStatus === 3)) {
                        return Dialog.alert({
                            title: t('common.tip'),
                            confirmButtonText: t('common.reSubmit'),
                            message: t('common.reviewFailed') + '\n' + t('common.reviewReson') + res.data.kycAuditRemark,

                        }).then(() => {
                            router.push('/authentication')
                        })
                    } else if (Number(res.data.kycAuditStatus === 2)) {
                        Dialog.alert({
                            title: t('common.tip'),
                            confirmButtonText: t('common.ok'),
                            message: t('common.reviewSuccess'),

                        }).then(() => {
                            noticeSetPwd(res.data.loginPassStatus)
                        })
                    }
                } else if (Number(res.data.companyKycStatus) === 2) {
                    noticeSetPwd(res.data.loginPassStatus)
                }
            })
        }

        const noticeSetPwd = (loginPassStatus) => {
            if (parseInt(loginPassStatus) === 1 && !localGet('loginPwdIgnore')) {
                state.loginPwdPop = true
            } else {
                loginToPath()
            }
        }

        const topRightClick = () => {
            console.log('rightClick')
        }

        // 发送验证码
        const verifyCodeSendHandler = (callback) => {
            const verifyParams = {
                type: state.loginName.includes('@') ? 1 : 2,
                loginName: state.loginName
            }

            const validator = new Schema(RuleFn(t))
            validator.validate({
                ...verifyParams
            }).then(res => {
                // 检测客户是否存在,同时获取区号
                checkUserStatus(verifyParams).then(res => {
                    if (res.check()) {
                        if (Number(res.data.status) === 2) {
                            const msg = t(verifyParams.type === 1 ? 'common.noEmail' : 'common.noPhone')
                            return Toast(msg)
                        } else if (Number(res.data.status === -1)) {
                            return Toast(t('c.userDisable'))
                        } else {
                            state.zone = res.data.phoneArea
                            const params = {
                                bizType: state.loginName.includes('@') ? 'EMAIL_LOGIN_VERIFICATION_CODE' : 'SMS_LOGIN_VERIFICATION_CODE',
                                toUser: state.loginName.includes('@') ? state.loginName : String(state.zone) + ' ' + state.loginName,
                            }
                            verifyCodeSend(params).then(res => {
                                if (res.check()) {
                                    token = res.data.token
                                    // if (res.data.code) state.checkCode = res.data.code
                                    callback && callback()
                                }
                            })
                        }
                    }
                })
            }).catch(({
                errors,
                fields
            }) => {
                if (errors) {
                    Toast(errors[0].message)
                }
            })
        }

        // 设置登录密码
        const loginPwdSet = () => {
            state.loginPwdPop = false
            router.push({
                name: 'SetLoginPwd'
            })
        }
        // 下次设置登录密码
        const loginPwdSetNext = () => {
            state.loginPwdPop = false
            loginToPath()
        }
        // 设置登录密码不再提醒
        const noTip = () => {
            localSet('loginPwdIgnore', true)
            state.loginPwdPop = false
            loginToPath()
        }

        return {
            ...toRefs(state),
            changeLoginType,
            rightAction,
            loginHandle,
            topRightClick,
            verifyCodeSendHandler,
            loginPwdSetNext,
            loginPwdSet,
            noTip,
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/sass/mixin.scss';
.pageWrap {
    position: relative;
    height: 100%;
    background: var(--contentColor);
    .header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: rem(40px) rem(30px);
    }
    .pageTitle {
        margin-bottom: rem(10px);
        font-weight: normal;
        font-size: rem(50px);
    }
}
.icon_icon_close_big {
    position: absolute;
    top: rem(30px);
    left: rem(30px);
    color: var(--color);
    font-size: rem(34px);
}
.loginForm {
    margin: rem(50px) rem(30px);
    .field {
        position: relative;
        display: flex;
        align-items: center;
        &:not(:first-of-type) {
            margin-top: rem(45px);
        }
        &.toolWrap {
            justify-content: space-between;
        }
        label {
            position: absolute;
            top: 0;
            left: 0;
            height: rem(75px);
            color: var(--placeholder);
            font-size: rem(30px);
            line-height: rem(75px);
            transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.15s;
            &.active {
                transform: scale(0.8) translateY(-90%);
            }
        }
        .input {
            display: block;
            flex: 1;
            height: rem(75px);
            font-size: rem(30px);
            line-height: 1;
            &:focus~label,
            &:valid~label {
                transform: scale(0.8) translateY(-90%);
                transform-origin: bottom left;
            }
        }
        .van-icon-clear {
            color: var(--lineColor);
            font-size: rem(36px);
        }
        .icon_icon_default,
        .icon_icon_pressed {
            margin-left: rem(10px);
            color: var(--lineColor);
            font-size: rem(36px);
        }
    }
    .loginBtn {
        width: 100%;
        height: rem(100px);
        margin-top: rem(90px);
        color: var(--color);
        font-size: rem(30px);
        line-height: rem(80px);
        background: var(--lineColor);
        border-color: var(--lineColor);
        border-radius: rem(4px);
        &.light {
            margin-top: rem(40px);
            color: var(--primary);
            background: none;
        }
    }
}
.tools {
    font-size: rem(26px);
    // margin-top: rem(50px);
    text-align: center;
    .line {
        margin: 0 1em;
        color: var(--minorColor);
    }
}
.otherLogin {
    margin-top: rem(30px);
    text-align: center;
    .empty {
        display: inline-block;
        width: rem(50px);
        height: rem(30px);
        vertical-align: middle;
    }
}
.footer {
    position: absolute;
    bottom: 20px;
    left: 0;
    width: 100%;
    text-align: center;
    .icon_icon_service {
        font-size: 1.2em;
        vertical-align: middle;
    }
}
.toolBtns {
    margin-top: rem(30px);
    text-align: center;
    .btn {
        @include active();
        color: var(--color);
        vertical-align: middle;
    }
}
.popContainer {
    position: relative;
    width: 80vw;
    background: var(--contentColor);
    border-radius: 8px;
    .kycTimeLine {
        padding: rem(60px);
    }
    .containerBox {
        padding: rem(80px) rem(60px);
    }
    .tipContent {
        font-size: rem(28px);
        line-height: 1.5;
        text-align: center;
    }
    .iconPwd {
        padding-bottom: rem(35px);
        text-align: center;
    }
    .icon_password {
        color: var(--primary);
        font-size: rem(60px);
    }
    .noTip {
        position: absolute;
        top: rem(20px);
        right: rem(20px);
        color: var(--minorColor);
    }
    .btnBox {
        position: relative;
        display: flex;
        color: var(--color);
        font-size: rem(34px);
        text-align: center;
        background: var(--bgColor);
        border-top: 1px solid var(--lineColor);
        &::after {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 1px;
            height: rem(50px);
            background: var(--lineColor);
            transform: translateY(-50%);
            content: '';
        }
        .btn {
            @include active();
            flex: 1;
            height: rem(100px);
            line-height: 1;
            background: none;
        }
    }
}
</style>
